.npcsheet-backgroundImage {
    position: absolute;
    height: 100%;
    width: 100%;
    overflow-x: scroll;
}

.npcsheet {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    background: url(https://www.dndbeyond.com/content/1-0-1893-0/skins/waterdeep/images/mon-summary/stat-block-top-texture.png), url(https://www.dndbeyond.com/content/1-0-1893-0/skins/waterdeep/images/mon-summary/paper-texture.png);
    background-size: 100% auto;
    background-position: top center;
    background-repeat: no-repeat, repeat;
    box-shadow: 0 0 5px #979aa4;
    border: 1px solid #d4d0ce;
    padding: 15px;
    margin: 15px;
    font-family: "Scala Sans Offc", Roboto, Helvetica, sans-serif;
    font-size: 15px;
    width: 80%;
}

.npcsheet-separator {
    margin-top: 15px;
    margin-bottom: 15px;
}

.npcsheet-header {
    font-weight: bold;
    font-size: 34px;
    font-family: MrsEavesSmallCaps, Roboto, Helvetica, sans-serif;
    color: #822000;
}

.npcsheet-header-meta {
    font-size: 15px;
    color: black;
}

.npcsheet-attributes {
    font-size: 15px;
    font-weight: bold;
    color: #822000;
}

.npcsheet-array {
    font-size: 15px;
    font-weight: bold;
    color: #822000;
}

.npcsheet-tidbits {
    font-size: 15px;
    font-weight: bold;
    color: #822000;
}

.npcsheet-statblock {
    display: inline-block;
}

.npcsheet-statblock-header {
    font-weight: bold;
    font-size: 24px;
    color: #822000;
    border-bottom: 1px solid;
    line-height: 1.4;
}

.npcability {
    font-weight: bold;
    margin-right: 0px;
    text-align: center;
}

.npcability:hover {
    cursor: pointer;
    text-decoration: underline;
}

.npctidbit-type {
    margin-right: 5px;
}

.npctidbit {
    font-weight: bold;
    margin-right: 0px;
}

.npctidbit:hover {
    cursor: pointer;
    text-decoration: underline;
}

.npcitem-type {
    margin-right: 5px;
}

.npcitem {
    font-weight: bold;
    margin-right: 0px;
    color: black;
}

.npcitem:hover {
    cursor: pointer;
    text-decoration: underline;
}

.npc-sidebaritem {
    width: 30%;
}

.npcsheet-row {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

.npc-columnsheet {
    width: 50%;
}

.npcsheet-columnsidebar {
    font-style: normal;
    font-weight: 700;
    font-size: 15px;
    width: 50%;
}

.npcsheet-columnsidebar-itemholder {
    bottom: 1%;
    margin: 3%;
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 0.8vh;
}

.npcsheet-columnsidebar-collapse-button>button {
    background: #182026;
    color: white;
    border-radius: 21px;
    border: none;
    letter-spacing: 1px;
    padding: 0.8vh;
}

.npcsheet-columnsidebar-item {
    background: #182026;
    color: white;
    padding: 2.5vh;
    border-radius: 15px;
    letter-spacing: 1px;
    box-shadow: 0 0 5px #979aa4;
}

.npcsheet-columnsidebar-portrait>img {
    border-color: #822000;
    border-width: 3px;
    border-style: solid;
    border-radius: 15px;
}