.charactercard {
    padding: 0px;
    margin: 0px;
    border-style: solid;
    border-width: 2px;
    border-color: rgb(146, 146, 146);
    margin: 7px;
}

.charactercard-header-cover {
    background: url(https://www.dndbeyond.com/avatars/13572/305/637396106028158757.jpeg);
    background-position: center center;
    background-size: cover;
    filter: blur(2px);
    position: relative;
    overflow: hidden;
    height: 120px;
}

.charactercard-header-upper {
    background-color: rgba(0, 0, 0, 0);
    transform: translate(3%, -175%);
    position: absolute;
    margin-top: 30px;
    margin-left: 20px;
    z-index: 0;
}

.charactercard-header-upper-name {
    font-size: 17px;
    color: white;
    font-weight: bold;
    margin-left: 10px;
    margin-top: 10px;
}

.charactercard-header-upper-name>p {
    font-size: 11px;
    color: rgb(201, 201, 201);
}

.charactercard-header-upper-image {
    background-image: url(https://www.dndbeyond.com/avatars/23603/245/1581111423-67165989.jpeg?width=60&height=60&fit=bounds&quality=95&auto=webp);
    width: 60px;
    height: 60px;
    background-position: center center;
    background-size: cover;
    border-radius: 5px;
}

.charactercard-button {
    border: none;
    font-size: 11px;
    font-weight: bold;
    color: #0c5cd3;
    background-color: white;
    border-radius: 3px;
    padding: 7px;
    margin: 7px;
    cursor: pointer;
}

.charactercard-button:hover {
    background-color: rgb(117, 117, 117);
}

.charactercard-delete-button {
    border: none;
    font-size: 11px;
    font-weight: bold;
    color: #bd0303;
    background-color: white;
    border-radius: 3px;
    padding: 10px;
    margin: 10px;
    cursor: pointer;
}

.charactercard-delete-button:hover {
    background-color: rgb(117, 117, 117);
}